.header {
  padding: 0 !important;
  margin: 0 !important;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100vw;
  height: 35rem;
  position: relative;
}

.filter {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.header::before {
  content: "";
  display: block;
  position: absolute;
  background-color: #000;
  opacity: 0.3;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 500px) {
  .header {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100vw;
    height: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
